<template>
  <div>
    <el-button type="primary" @click="willAdd">添加</el-button>
    <v-list :list="list" @edit="willUpdate" @init="getInit"></v-list>
    <v-add :info="info" ref="add" @init="getInit"></v-add>

    <el-pagination
      background
      layout="prev, pager, next"
      :page-size="size"
      @current-change="changePage"
      :total="total"
    ></el-pagination>
  </div>
</template>
<script>
import vList from "./components/list.vue";
import vAdd from "./components/add.vue";
import { reqMangerList, reqMangerConut } from "../../util/request";
export default {
  components: {
    vAdd,
    vList
  },
  data() {
    return {
      info: {
        isshow: false,
        title: "用户修改"
      },
      list: [],
      total: 0,
      size: 2,
      page: 1
    };
  },
  methods: {
    willAdd() {
      this.info = {
        isshow: true,
        title: "用户修改"
      };
      this.$refs.add.empty();
    },
    getInit() {
      reqMangerConut().then(res => {
        if (res.data.code === 200) {
          this.total = res.data.list[0].total;
        }
      });
      this.getList();
    },
    getList() {
      reqMangerList({ page: this.page, size: this.size }).then(res => {
        if (res.data.code === 200) {
          //   this.list = res.data.list;
          let list = res.data.list ? res.data.list : [];
          if (list.length === 0 && this.page > 1) {
            this.page--;
            this.getList();
            return;
          }
          this.list = list
        }
      });
    },
    changePage(page) {
      this.page = page;
      this.getList();
    },
    willUpdate(uid) {
      this.info = {
        isshow: true,
        title: "用户编辑"
      };
      this.$refs.add.getOne(uid);
    }
  },
  mounted() {
    this.getInit();
  }
};
</script>
<style scoped>
</style>


