<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.isshow">
      <el-form :model="form">
        <el-form-item label="所属角色" label-width="120px">
          <el-select v-model="form.roleid" placeholder="请选择活动区域">
            <el-option label="--请选择--" value disabled></el-option>
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.rolename"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="用户名" label-width="120px">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" label-width="120px">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="状态" label-width="120px">
          <el-switch v-model="form.status" :active-value="1" :inactive-value="2"></el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="add" v-if="info.title=='用户修改'">添 加</el-button>
        <el-button type="primary" @click="update" v-else>修 改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapActions, mapGetters } from "vuex";
import {
  reqMangerAdd,
  reqMangerListOne,
  reqMangerUpdate
} from "../../../util/request";
import { alertSuccess, alertWarning } from "../../../util/alert";
export default {
  props: ["info", "list"],
  components: {},
  computed: {
    ...mapGetters({
      roleList: "role/role"
    })
  },
  data() {
    return {
      form: {
        roleid: "",
        username: "",
        password: "",
        status: 1
      }
    };
  },
  methods: {
    //验证
    checked() {
      return new Promise((resolve, reject) => {
        //验证数据是否均不为空
        if (this.form.roleid === "") {
          alertWarning("所属角色不能为空");
          return;
        }
        if (this.form.username === "") {
          alertWarning("用户名不能为空");
          return;
        }
        if (this.form.password === "" || this.form.password === "undefined") {
          alertWarning("密码不能为空");
          return;
        }
        resolve();
      });
    },
    empty() {
      this.form = {
        roleid: "",
        username: "",
        password: "",
        status: 1
      };
    },
    add() {
      this.checked().then(() => {
        reqMangerAdd(this.form).then(res => {
          if (res.data.code === 200) {
            alertSuccess(res.data.msg);
            this.cancel();
            this.empty();
            this.$emit("init");
          }
        });
      });
    },
    getOne(uid) {
      reqMangerListOne(uid).then(res => {
        this.form = res.data.list;
        this.form.password = "";
      });
    },
    update() {
      this.checked().then(() => {
        reqMangerUpdate(this.form).then(res => {
        if (res.data.code === 200) {
          this.cancel();
          alertSuccess(res.data.msg);
          this.empty();
          this.$emit("init");
        } else {
          alertWarning(res.data.msg);
        }
      });
      })
      
    },
    cancel() {
      this.info.isshow = false;
    },
    ...mapActions({
      requsetRoleList: "role/requsetRoleList"
    })
  },
  mounted() {
    this.requsetRoleList();
  }
};
</script>
<style scoped>
</style>


